html,body{margin:0;padding:0;height:100%}*{font-family:'Open Sans', sans-serif;box-sizing:border-box;outline:none}h1,h2,h3,h4,h5,h6{font-weight:400;color:#0C0C0C;letter-spacing:0.5px;margin:0;padding:0}p{line-height:1.75;word-spacing:2px}a{color:#0AF}.text-disable{opacity:0.5}.button{width:auto;height:auto;cursor:pointer;font-size:0.95rem;font-weight:600;margin:0.25rem 0.35rem;padding:0.35rem 1rem;background:none;border-radius:5px;border:3px solid #333;letter-spacing:1px;-webkit-transition:all 0.3s ease;transition:all 0.3s ease}.button-default{border-color:#333;color:#333}.button-default:hover{border-color:transparent;background:#333;color:#fff}.button-active{border-color:#5BC8FF;color:#5BC8FF}.button-active:hover{border-color:transparent;background:#5BC8FF;color:#fff}.button-warning{border-color:#FCD453;color:#FCD453}.button-warning:hover{border-color:transparent;background:#FCD453;color:#fff}.button-danger{border-color:#FF778C;color:#FF778C}.button-danger:hover{border-color:transparent;background:#FF778C;color:#fff}.button-disable{cursor:not-allowed;border-color:#333;color:#333;opacity:0.5}.upload{width:0.1px;height:0.1px;opacity:0;overflow:hidden;position:absolute;z-index:-1}.upload+label{width:auto;height:auto;cursor:pointer;font-size:0.95rem;font-weight:600;padding:0.35rem 1rem;background:none;border-radius:5px;border:3px solid #333;letter-spacing:1px;-webkit-transition:all 0.25s ease;transition:all 0.25s ease}.upload:focus+label,.upload+label:hover{border-color:#333;color:#333}.upload:focus+label:hover,.upload+label:hover:hover{border-color:transparent;background:#333;color:#fff}.upload:focus+label{outline:1px dotted #000;outline:-webkit-focus-ring-color auto 5px}.upload-default{border-color:#333;color:#333}.upload-default:hover{border-color:transparent;background:#333;color:#fff}.upload-active{border-color:#5BC8FF;color:#5BC8FF}.upload-active:hover{border-color:transparent;background:#5BC8FF;color:#fff}.dragImg-area{width:100%;height:auto;min-height:200px;max-width:600px;position:relative;border:3px dashed #b3b3b3;border-radius:5px;-webkit-transition:all 0.25s ease;transition:all 0.25s ease;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;-webkit-box-align:center;-ms-flex-align:center;align-items:center}.dragImg-area:hover{border-width:3px;border-color:#333}.dragImg-area:hover .dragImg-title{color:#1a1a1a}.dragImg-area input{position:absolute;cursor:pointer;top:0;left:0;width:100%;height:100%;opacity:0}.dragImg-preview{width:100%;height:auto;padding:15px;border:3px solid #333;border-radius:5px;position:relative}.dragImg-preview img{width:100%;border-radius:5px}.modal-close{height:50px;width:50px;position:absolute;top:50%;left:50%;border-radius:50%;border:3px solid #b3b3b3;-webkit-transform:translate(-25px, -25px);transform:translate(-25px, -25px);-webkit-transition:all .25s linear;transition:all .25s linear}.modal-close:hover{cursor:pointer;border-color:#FF778C}.modal-close:hover span{background-color:#FF778C}.modal-close span{position:absolute;z-index:1;width:3px;height:30px;top:7px;margin-left:20px;border-radius:10px;background-color:#b3b3b3;-webkit-transition:all .25s linear;transition:all .25s linear;-webkit-transform-origin:50% 50%;transform-origin:50% 50%;display:block}.modal-close span:first-child{-webkit-transform:rotate(45deg);transform:rotate(45deg)}.modal-close span:last-child{-webkit-transform:rotate(135deg);transform:rotate(135deg)}.loader{width:50px;height:50px;position:relative}.loader span{background:#5bc8ff;position:absolute;border-radius:50%;top:50%;left:50%;-webkit-animation:loading 1s ease-in-out infinite;animation:loading 1s ease-in-out infinite}.loader span:nth-child(1){width:80%;height:80%;margin-top:-40%;margin-left:-40%;-webkit-animation-delay:0s;animation-delay:0s}.loader span:nth-child(2){background:#00a2f4;width:50%;height:50%;margin-top:-25%;margin-left:-25%;-webkit-animation-delay:0.25s;animation-delay:0.25s}.loader.loader-bg{width:70px;height:70px}.loader.loader-bg span:nth-child(3){background:#005e8e;width:25%;height:25%;margin-top:-12.5%;margin-left:-12.5%;-webkit-animation-delay:0.35s;animation-delay:0.35s}@-webkit-keyframes loading{0%{-webkit-transform:scale(1);transform:scale(1);opacity:0.8}50%{-webkit-transform:scale(1.1);transform:scale(1.1);opacity:1}100%{-webkit-transform:scale(1);transform:scale(1);opacity:0.8}}@keyframes loading{0%{-webkit-transform:scale(1);transform:scale(1);opacity:0.8}50%{-webkit-transform:scale(1.1);transform:scale(1.1);opacity:1}100%{-webkit-transform:scale(1);transform:scale(1);opacity:0.8}}.demo-wrapper{width:100%;margin-top:150px;max-width:600px;padding:0 15px}.demo-wrapper .demo-section{padding-bottom:4rem;text-align:left}.demo-wrapper .demo-header{margin-bottom:1rem;padding-bottom:0.25rem;border-bottom:1px solid #f2f2f2}.demo-wrapper .demo-header h1{font-size:1.25em}.demo-wrapper .demo-text{margin-bottom:0.7rem}#app{color:#333;width:100vw;height:100vh;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:vertical;-webkit-box-direction:normal;-ms-flex-direction:column;flex-direction:column;-webkit-box-pack:start;-ms-flex-pack:start;justify-content:flex-start;-webkit-box-align:center;-ms-flex-align:center;align-items:center;font-family:Source Sans Pro, Helvetica, sans-serif;text-align:center}.logo{width:100px;height:100px}



